////\n\n$breakpoint_xs: 580px;\n$breakpoint_sm: 768px;\n$breakpoint_md: 1024px;\n$breakpoint_lg: 1281px;\n\n// Colors\n// //////////////////////////////////////////\n\n$color_white: #ffffff;\n$color_black: #353b48;\n$color_cenere: #e8ebf1;\n$color_primary: #273c75;\n$color_blue: #0097e6;\n$color_green: #44bd32;\n$color_yellow: #e1b12c;\n$color_orange: #c23616;\n\n// Sizes\n// //////////////////////////////////////////\n\n$size_header_height: 50px;\n$size_sidebar_width: 200px;\n","// Configuration\n// *********************************************************************\n\n$grid: (\n  columns: 24,\n  space: 7.5px,\n  breakpoints: (\n    xs: 581px,\n    sm: 769px,\n    md: 1025px,\n    lg: 1282px\n  )\n);\n\n// Style\n// *********************************************************************\n\n.c-grid__container, .c-grid__container-spaced {\n  margin: auto;\n  box-sizing: border-box;\n  @include clearfix;\n}\n\n.c-grid__col {\n  float: left;\n  box-sizing: border-box;\n  width: 100%;\n}\n\n// Basic grid\n// *********************************************************************\n\n@for $i from 1 through map-get($grid, 'columns') {\n  $width: percentage(1 / (map-get($grid, 'columns') / $i));\n\n  .c-grid__col--#{$i} {\n    width: $width;\n  }\n}\n\n@each $name, $pixels in map-get($grid, 'breakpoints') {\n  @media screen and (min-width: $pixels) {\n    @for $i from 1 through map-get($grid, 'columns') {\n      $width: percentage(1 / (map-get($grid, 'columns') / $i));\n    \n      .c-grid__col--#{$name}-#{$i} {\n        width: $width;\n      }\n    }\n  }\n}\n\n// Spaced grid\n// *********************************************************************\n\n.c-grid__container-spaced {\n  margin-left: - map-get($grid, 'space');\n  margin-right:  - map-get($grid, 'space');\n\n  > .c-grid__col {\n    margin-left: map-get($grid, 'space');\n    margin-right: map-get($grid, 'space');\n  }\n\n  @for $i from 1 through map-get($grid, 'columns') {\n    $width: percentage(1 / (map-get($grid, 'columns') / $i));\n  \n    > .c-grid__col--#{$i} {\n      width: calc(#{$width} - #{map-get($grid, 'space') * 2});\n    }\n  }\n  \n  @each $name, $pixels in map-get($grid, 'breakpoints') {\n    @media screen and (min-width: $pixels) {\n      @for $i from 1 through map-get($grid, 'columns') {\n        $width: percentage(1 / (map-get($grid, 'columns') / $i));\n      \n        > .c-grid__col--#{$name}-#{$i} {\n          width: calc(#{$width} - #{map-get($grid, 'space') * 2});\n        }\n      }\n    }\n  }\n}",".c-section {\n  width: calc(100% - #{$size_sidebar_width});\n  min-height: calc(100vh - 50px);\n  margin-left: $size_sidebar_width;\n  padding: 15px;\n  transition: all 0.3s;\n  position: relative;\n  padding-top: 65px;\n\n  &--full {\n    width: 100%;\n    margin-left: 0;\n  }\n}\n\n.c-section__head {\n  border-bottom: solid 1px darken($color_cenere, 5%);\n  padding-bottom: 5px;\n}\n\n.c-section__head-title {\n  display: inline-block;\n  margin: 0;\n  font-size: 30px;\n  color: $color_black;\n}\n\n.c-section__head-breadcrumb {\n  padding-top: 13px;\n  float: right;\n}\n\n.c-section__head-breadcrumb-link {\n  font-size: 14px;\n  color: $color_black;\n  font-weight: bold;\n  text-decoration: none;\n\n  &:after {\n    color: darken($color_cenere, 20%);\n    content: ' - ';\n  }\n\n  &:last-child {\n    &:after { display: none; }\n  }\n\n  &[href] {\n    color: darken($color_cenere, 20%);\n    font-weight: normal;\n\n    &:hover {\n      color: $color_primary;\n\n      &:after {\n        color: darken($color_cenere, 20%);\n      }\n    }\n  }\n}\n\n.c-section__content {\n  margin-top: 15px;\n\n  &--cols {\n    display: flex; /* [1] */\n    flex-wrap: nowrap; /* [1] */\n    overflow-x: auto; /* [1] */\n    -webkit-overflow-scrolling: touch; /* [4] */\n    -ms-overflow-style: -ms-autohiding-scrollbar; /* [5] */\n  }\n}\n\n.c-section__content-block {\n  margin-bottom: 15px;\n\n  &--col {\n    flex: 0 0 auto;\n    width: 325px;\n    margin-right: 15px;\n\n    > .c-