ursor: pointer;\n  transition: background-color 0.3s;\n  text-decoration: none;\n\n  &:hover {\n    background-color: $color_cenere;\n  }\n}\n\n@media (max-width: $breakpoint_sm) {\n  .c-header__nav-menu {\n    position: fixed;\n    width: calc(100% - 30px);\n    right: 15px;\n    max-height: calc(100% - #{$size_header_height} - 20px);\n  }\n}\n",".c-footer {\n  width: calc(100% - #{$size_sidebar_width});\n  height: 50px;\n  margin-left: $size_sidebar_width;\n  padding: 0 15px;\n  transition: all 0.3s;\n\n  &--full {\n    width: 100%;\n    margin-left: 0;\n  }\n}\n\n.c-footer__content {\n  padding: 15px 0;\n  font-size: 12px;\n  color: $color_black;\n  border-top: solid 1px darken($color_cenere, 5%);\n\n  a {\n    color: $color_black;\n  }\n}\n\n.is-menu-closed {\n  .c-footer {\n    width: 100%;\n    margin-left: 0;\n  }\n}\n\n.is-primary {\n  .c-footer__content {\n    color: $color_white;\n\n    a {\n      color: $color_white;\n    }\n  }\n}\n\n@media (max-width: $breakpoint_sm) {\n  .c-footer {\n    width: 100%;\n    margin-left: 0;\n\n    &--full {\n      width: 100%;\n      margin-left: 0;\n    }\n  }\n}",".c-sidebar {\n  position: fixed;\n  top: 50px;\n  left: 0;\n  width: $size_sidebar_width;\n  height: calc(100% - #{$size_header_height});\n  background-color: $color_white;\n  z-index: 3;\n  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.05);\n  transition: all 0.3s;\n  overflow-y: auto;\n}\n\n.c-sidebar__list {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  margin-top: 15px;\n}\n\n.c-sidebar__list-item {\n  position: relative;\n\n  &:before {\n    content: '';\n    display: block;\n    position: absolute;\n    top: 0; left: 0;\n    width: 5px;\n    height: 100%;\n    background-color: darken($color_cenere, 10%);\n  }\n\n  &[data-disabled] {\n    pointer-events: none;\n    \n    .c-sidebar__list-link {\n      color: rgba($color_black, 0.4);\n    }\n  }\n\n  &[data-active] {\n    i {\n      color: $color_white;\n    }\n\n    &:before {\n      background-color: $color_primary;\n    }\n\n    .c-sidebar__list-link {\n      color: $color_white;\n      background-color: $color_primary;\n      border-bottom-color: $color_primary;\n    }\n  }\n\n  &:hover {\n    &:before {\n      background-color: $color_primary;\n    }\n  }\n}\n\n.c-sidebar__list-link {\n  display: block;\n  padding: 17px 15px;\n  border-bottom: solid 1px darken($color_cenere, 5%);\n  color: $color_black;\n  text-decoration: none;\n  font-size: 16px;\n  font-weight: normal;\n  transition: background-color 0.3s;\n\n  &:hover {\n    background-color: $color_cenere;\n  }\n\n  i {\n    width: 25px;\n    color: darken($color_cenere, 20%);\n    text-align: center;\n  }\n\n  span {\n    display: inline-block;\n    margin-left: 5px;\n  }\n}\n\n.is-menu-closed {\n  .c-sidebar {\n    transform: translateX(-100%);\n    box-shadow: none;\n  }\n}\n\n@media (max-width: $breakpoint_sm) {\n  .c-sidebar {\n    width: 80%;\n    box-shadow: 0 5px 15px 0 rgba(0,0,0,0.1);\n  }\n}\n",".c-flash {\n  position: fixed;\n  top: $size_header_height + 15px;\n  right: 0;\n  width: 300px;\n  z-index: 5;\n}\n\n.c-flash__message {\n  width: 100%;\n  background-color: $color_primary;\n  color: $color_white;\n  font-size: 14px;\n  text-align: left;\n  padding: 15px;\n  transition: all 0.3s;\n  transform: translateX(100%);\n  border-radius: 15px;\n  margin-bottom: 5px;\n  position: relative;\n  box-shadow: 0 5px 5px 0 rgba(0,0,0,0.05);\n\n  &.is-active {\n    transform: translateX(-15px);\n  }\n\n  &--green {\n    background-color: $color_green;\n\n    .c-flash__message-close { border-color: $color_green; }\n  }\n\n  &--orange {\n    background-color: $color_orange !important;\n\n    .c-flash__message-close { border-color: $color_orange; }\n  }\n\n  &--blue {\n    background-color: $color_blue;\n\n    .c-flash__message-close { border-color: $color_blue; }\n  }\n\n  &--yellow {\n    background-color: $color_yellow;\n\n    .c-flash__message-close { border-color: $color_yellow; }\n  }\n}\n\n.c-flash__message-close {\n  display: block;\n  width: 20px;\n  height: 20px;\n  position: absolute;\n  top: -5px;\n  left: -5px;\n  background-color: $color_white;\n  border-radius: 50%;\n  border: solid 2px $color_primary;\n  cursor: pointer;\n\n  &:hover {\n    background-color: darken($color_white, 5%);\n  }\n\n  &:before, &:after {\n    content: '';\n    display: block;\n    width: 2px;\n    height: 10px;\n    background-color: darken($color_cenere, 30%);\n    position: absolute;\n    top: calc(50% - 5px);\n    left: calc(50% - 1px);\n  }\n\n  &:before {\n    transform: rotate(45deg);\n  }\n\n  &:after {\n    transform: rotate(-45deg);\n  }\n}\n",".c-card {\n  background-color: $color_white;\n  border-radius: 10px;\n  overflow: hidden;\n  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.05);\n  transition: box-shadow 0.25s linear;\n\n  &--centerpage { // center the card on the page\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    width: 600px;\n  }\n\n  &--centerpage-large { // center the card on the page with a large size\n    width: 900px;\n  }\n\n  &--unhidden { // permit overflow on card\n    overflow: visible;\n  }\n\n  &--hasnext { // add a margin for the next card\n    margin-bottom: 15px;\n  }\n\n  &--viewported { // add viewported support for viewport view\n    &.is-fixed {\n      position: fixed;\n      top: 0;\n      left: 0;\n      z-index: 9999;\n      width: 100%;\n      height: 100vh;\n      border-radius: 0;\n      overflow: auto;\n\n      .c-card__header {\n        border-radius: 0;\n      }\n\n      .c-card__body--scrollable {\n        max-height: calc(100vh - 50px);\n      }\n    }\n  }\n\n  &:hover {\n    box-shadow: 0 5px 5px 0 rgba(0,0,0,0.05);\n  }\n}\n\n.c-card__header {\n  background-color: $color_white;\n  color: $color_black;\n  border-bottom: solid 1px darken($color_cenere, 5%);\n  border-top-left-radius: 10px;\n  border-top-right-radius: 10px;\n  @include clearfix;\n}\n\n.c-card__header-title {\n  display: inline-block;\n  font-size: 18px;\n  font-weight: bold;\n  padding: 15px;\n}\n\n.c-card__header-actions {\n  list-style: none;\n  margin: 0;\n  padding: 7px 15px;\n  float: right;\n  @include clearfix;\n\n  &--full {\n    float: none;\n    width: 100%;\n  }\n}\n\n.c-card__header-action {\n  display: inline-block;\n  margin-right: 5px;\n\n  &--full {\n    display: block;\n    width: 100%;\n  }\n\n  &:last-child {\n    margin-right: 0;\n  }\n\n  .c-button {\n    padding: 8px 20px;\n  }\n\n  .c-input-action {\n    margin: 2px 0;\n    font-size: 14px;\n\n    .c-input__field, .c-input-action__field {\n      padding: 7px 10px;\n    }\n    .c-input-action__button {\n      padding: 7px 0;\n    }\n  }\n}\n\n.c-card__header-action-text {\n  font-size: 12px;\n  display: inline-block;\n  margin-top: 10px;\n}\n\n.c-card__body {\n  padding: 15px;\n  border-bottom-left-radius: 10px;\n  border-bottom-right-radius: 10px;\n\n  &--list { // manage style for ul attribute\n    padding: 0;\n    margin: 0;\n    list-style: none;\n  }\n\n  &--scrollable { // permit scroll of content\n    max-height: calc(100vh - 200px);\n    overflow-y: auto;\n  }\n}\n\n.c-card__body-list-item {\n  border-bottom: solid 1px darken($color_cenere, 5%);\n  color: $color_black;\n\n  &:last-child {\n    border-bottom: none;\n  }\n\n  &--padding { // Use it when link or content is not present\n    padding: 15px;\n  }\n\n  &.is-active {\n    background-color: rgba($color_primary, 0.1);\n  }\n}\n\n.c-card__body-list-item-link {\n  display: block;\n  padding: 15px;\n  color: $color_black;\n  text-decoration: none;\n\n  &:hover {\n    background-color: $color_cenere;\n    text-decoration: none;\n  }\n\n  &.is-active {\n    background-color: rgba($color_primary, 0.1);\n  }\n}\n\n.c-card__body-list-item-content {\n  display: inline-block;\n  padding-right: 90px;\n}\n\n.c-card__body-list-item-action {\n  position: absolute;\n  top: 15px;\n  right: 15px;\n  font-size: 14px;\n  color: $color_primary;\n}\n\n@media (max-width: $breakpoint_md) {\n  .c-card {\n    &--centerpage-large {\n      width: calc(100% - 30px);\n    }\n  }\n}\n\n@media (max-width: $breakpoint_sm) {\n  .c-card {\n    background-color: $color_white;\n    border-radius: 0;\n    overflow: hidden;\n\n    &--centerpage {\n      position: relative;\n      top: 0;\n      left: 0;\n      transform: none;\n      width: calc(100% - 30px);\n      margin: auto;\n      border-radius: 15px;\n      margin-top: $size_header_height + 15px;\n      margin-bottom: 15px;\n    }\n\n    &--centerpage-large {\n      width: calc(100% - 30px);\n    }\n  }\n\n  .c-card__header {\n    border-radius: 0;\n  }\n\n  .c-card__header-title {\n    display: block;\n    text-align: center;\n    font-size: 20px;\n  }\n\n  .c-card__header-actions {\n    text-align: center;\n    float: none;\n    padding-top: 0;\n    margin-top: -10px;\n\n    &--full {\n      margin-top: 0;\n      padding-top: 7px;\n    }\n  }\n\n  .c-card__header-actions--group {\n    display: flex;\n  }\n\n  .c-card__header-action {\n    float: none;\n    width: 100%;\n\n    .c-button {\n      display: block;\n      text-align: center;\n      width: 100%;\n    }\n  }\n\n  .c-card__body {\n    border-bottom-left-radius: 0;\n    border-bottom-right-radius: 0;\n\n    &--scrollable {\n      max-height: none;\n      overflow-y: hidden;\n    }\n  }\n}\n\n@media (max-width: $breakpoint_sm) {\n  .c-card--mobile-fixed-section { // use it on sections that contains only a card mobile fixed\n    margin: 0 !important;\n  }\n  .c-card--mobile-fixed {\n    position: fixed;\n    top: $size_header_height;\n    left: 0;\n    width: 100%;\n    height: calc(100% - #{$size_header_height});\n    transform: translateX(100%);\n    transition: transform 0.3s;\n    overflow-y: auto;\n    z-index: 1;\n\n    &.is-open {\n      transform: translateX(0);\n    }\n\n    .c-card__header-actions {\n      margin-top: 0;\n      padding-top: 7px;\n    }\n\n    .c-card__body {\n      &--scrollable {\n        max-height: none;\n        overflow-y: hidden;\n      }\n    }\n  }\n\n  .c-card__header-title--mobile-fixed {\n    position: fixed;\n    bottom: 15px;\n    right: 15px;\n    background-color: $color_primary;\n    color: $color_white;\n    border-radius: 10px;\n    font-size: 12px;\n    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.10);\n    z-index: 2;\n  }\n}","@keyframes c-button-is-loading {\n  0%   { transform: translateX(-100%); }\n  100% { transform: translateX(200%); }\n}\n\n.c-button {\n  display: inline-block;\n  padding: 10px 20px;\n  margin: 2px 0;\n  cursor: pointer;\n  border-radius: 5px;\n  font-size: 14px;\n  background-color: $color_primary;\n  color: $color_white;\n  text-decoration: none;\n  border: none;\n  transition: all 0.15s;\n  outline: 0;\n  position: relative;\n\n  &[disabled] {\n    background-color: rgba($color_black, 0.65);\n\n    &:hover {\n      background-color: rgba($color_black, 0.65);\n      box-shadow: none;\n    }\n  }\n\n  &.is-loading {\n    color: rgba($color_white, 0.5);\n    overflow: hidden;\n\n    &:before {\n      content: '';\n      display: block;\n      width: 70%;\n      height: 2px;\n      background-color: rgba($color_white, 0.8);\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      animation: c-button-is-loading 1s infinite;\n    }\n  }\n\n  &:hover {\n    box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_primary,.25);\n    background-color: darken($color_primary, 5%);\n  }\n\n  &:active, &:focus {\n    background-color: darken($color_primary, 10%);\n  }\n\n  &--green {\n    background-color: $color_green;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_green,.25);\n      background-color: darken($color_green, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_green, 10%);\n    }\n  }\n\n  &--blue {\n    background-color: $color_blue;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_blue,.25);\n      background-color: darken($color_blue, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_blue, 10%);\n    }\n  }\n\n  &--yellow {\n    background-color: $color_yellow;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_yellow,.25);\n      background-color: darken($color_yellow, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_yellow, 10%);\n    }\n  }\n\n  &--orange {\n    background-color: $color_orange;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_orange,.25);\n      background-color: darken($color_orange, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_orange, 10%);\n    }\n  }\n\n  &--black {\n    background-color: $color_black;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_black,.25);\n      background-color: darken($color_black, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_black, 10%);\n    }\n  }\n  \n  &--cenere {\n    background-color: $color_cenere;\n    color: $color_black;\n\n    &:hover {\n      box-shadow: 0 5px 15px rgba(0,0,0,.05), 0 4px 10px rgba($color_cenere,.25);\n      background-color: darken($color_cenere, 5%);\n    }\n  \n    &:active, &:focus {\n      background-color: darken($color_cenere, 10%);\n    }\n  }\n\n  &--transparent {\n    background-color: transparent;\n    color: $color_black;\n\n    &:hover {\n      background-color: transparent;\n      box-shadow: none;\n      text-decoration: underline;\n    }\n  \n    &:active, &:focus {\n      background-color: transparent;\n      box-shadow: none;\n      text-decoration: underline;\n    }\n  }\n\n  &--block {\n    display: block;\n    text-align: center;\n  }\n}\n\n@media (max-width: $breakpoint_sm) {\n\n  .c-button {\n    padding: 15px 20px;\n  }\n\n}","// Form\n// *********************************************************************\n\n.c-form {\n  padding-bottom: 15px;\n  @include clearfix;\n}\n\n.c-form__actions {\n  margin-bottom: -15px;\n  text-align: right;\n\n  .c-button {\n    margin-left: 15px;\n\n    &:first-child {\n      margin-left: 0;\n    }\n  }\n}\n\n.c-form__title {\n  font-weight: bold;\n  font-size: 18px;\n  color: $color_black;\n  border-bottom: solid 1px $color_cenere;\n  padding-bottom: 5px;\n  margin-bottom: 15px;\n}\n\n// Input normal\n// *********************************************************************\n\n.c-input {\n  margin-bottom: 15px;\n}\n\n.c-input__label {\n  font-size: 14px;\n  color: $color_black;\n  display: block;\n  margin-bottom: 4px;\n  margin-left: 4px;\n}\n\n.c-input__label-subtitle {\n  font-size: 12px;\n  color: darken($color_cenere, 20%);\n  margin-bottom: 4px;\n  margin-left: 4px; \n  display: block;\n}\n\n.c-input__field {\n  display: block;\n  border: solid 1px darken($color_cenere, 10%);\n  border-radius: 5px;\n  background-color: $color_white;\n  color: $color_black;\n  padding: 10px;\n  outline: none;\n  width: 100%;\n  transition: all 0.15s;\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n\n  &:active, &:focus {\n    border-color: lighten($color_primary, 20%);\n    box-shadow: 0 2px 5px darken($color_cenere, 10%);\n  }\n\n  &[disabled] {\n    background-color: darken($color_white, 2%);\n\n    &:active, &:focus {\n      border-color: darken($color_cenere, 10%);\n      box-shadow: none;\n    }\n  }\n\n  &[required] {\n    // TODO\n  }\n\n  &.is-error {\n    border-color: $color_orange;\n  }\n}\n\ntextarea.c-input__field {\n  min-height: 100px;\n}\n\ninput[type=\"date\"].c-input__field {\n  padding: 7px 10px;\n  padding-top: 8px;\n}\n\n// Input action\n// *********************************************************************\n\n.c-input-action {\n\n}\n\n.c-input-action__field {\n  display: inline-block;\n  border: solid 1px darken($color_cenere, 5%);\n  border-right: none;\n  border-top-left-radius: 5px;\n  border-bottom-left-radius: 5px;\n  background-color: $color_white;\n  color: $color_black;\n  padding: 10px;\n  outline: none;\n  width: calc(100% - 60px);\n  transition: all 0.15s;\n\n  &:active, &:focus {\n    border-color: lighten($color_primary, 20%);\n    box-shadow: 0 2px 5px darken($color_cenere, 10%);\n  }\n\n  &.is-error {\n    border-color: $color_orange;\n  }\n}\n\n.c-input-action__button {\n  cursor: pointer;\n  display: inline-block;\n  border: solid 1px $color_primary;\n  border-top-right-radius: 5px;\n  border-bottom-right-radius: 5px;\n  background-color: $color_primary;\n  color: $color_white;\n  padding: 10px 0;\n  outline: none;\n  width: 60px;\n  float: right;\n  transition: all 0.15s;\n\n  &:hover {\n    background-color: darken($color_primary, 5%);\n  }\n\n  &[disabled] {\n    background-color: $color_black;\n    border-color: $color_black;\n\n    &:hover {\n      background-color: $color_black;\n    }\n  }\n}\n\n// Input checkbox\n// *********************************************************************\n\n.c-input-checkbox {\n  position: relative;\n  margin-bottom: 15px;\n}\n\n.c-input-checkbox__label {\n  display: inline-block;\n  font-size: 14px;\n  color: $color_black;\n  padding-left: 25px;\n  cursor: pointer;\n  position: relative;\n\n  &:before{\n    content: '';\n    display: inline-block;\n    height: 15px;\n    width: 15px; \n    border: solid 2px darken($color_cenere, 5%);\n    background-color: $color_white;\n    position: absolute; \n    left: 0;\n    top: 0;\n    border-radius: 5px;\n    transition: all 0.3s ease;\n  }\n\n  &:hover {\n    &:before{\n      background-color: darken($color_cenere, 5%);\n    }\n  }\n\n  &:after {\n    content: '';\n    display: inline-block;\n    height: 15px;\n    width: 15px;\n    background-color: $color_primary;\n    position: absolute;\n    left: 2px;\n    top: 2px;\n    border-radius: 4px;\n  }\n}\n\n.c-input-checkbox__field {\n  position: absolute;\n  top: 0; left: 0;\n  opacity: 0;\n}\n\n.c-input-checkbox .c-input-c